import {
  Box,
  Button,
  Container,
  Input,
  Text,
  VStack,
  useDisclosure,
} from "@chakra-ui/react";
import { useForm } from "react-hook-form";
import { pattern } from "../../pattern";
import { useState } from "react";
import { Link } from "react-router-dom";
import { routes } from "../../routes";
import { Modals } from "../../components/Modals";
import { Header } from "../../components/Header";
import { HelmetTitle } from "../../components/HelmetTitle";
const Word = {
  Duplicate: "  .",
  Success: "  ",
};
export const Register = () => {
  const [canOpen, SetOpen] = useState(false);
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [resText, setText] = useState();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();
  const onRegister = (data) => {
    console.log(data);
    const result = data;
    if (window.localStorage.getItem(result.username)) {
      setText(Word.Duplicate);
      SetOpen(!canOpen);
    } else {
      window.localStorage.setItem(
        result.username,
        JSON.stringify({
          index: Date.now(),
          ...result,
        })
      );
      setText(Word.Success);
      SetOpen(!canOpen);
    }
    reset();
  };
  return (
    <Container height={"100vh"}>
      <HelmetTitle title={"Register"} />
      <Header />
      <Box as="form" onSubmit={handleSubmit(onRegister)}>
        <VStack spacing={3}>
          <Input
            placeholder=""
            {...register("email", {
              required: " ",
              minLength: {
                value: 4,
                message: "  .",
              },
              pattern: {
                value: pattern.email,
                message: "   ",
              },
            })}
          />
          <Text>{errors?.email?.message}</Text>
          <Input
            placeholder=""
            {...register("username", {
              required: " ",
              minLength: {
                value: 8,
                message: "8 .",
              },
              pattern: {
                value: pattern.id,
                message: "  .",
              },
            })}
          />
          <Text>{errors?.username?.message}</Text>
          <Input
            placeholder=""
            {...register("password", {
              required: " ",
              minLength: {
                value: 3,
                message: " 3 ",
              },
              pattern: {
                value: pattern.password,
                message: "  .",
              },
            })}
          />
          <Text>{errors?.password?.message}</Text>
          <Button
            colorScheme="blue"
            type="submit"
            onClick={() => {
              if (canOpen) {
                onOpen();
                SetOpen(false);
              }
            }}
          >
            
          </Button>
          <Button>
            <Link to={routes.home}> </Link>
          </Button>
          <Modals isopen={isOpen} onClose={onClose} Text={resText}></Modals>
        </VStack>
      </Box>
    </Container>
  );
};
