import {
  Avatar,
  Box,
  Button,
  Container,
  Text,
  VStack,
  useDisclosure,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
  Input,
  RadioGroup,
  Stack,
  Radio,
  Alert,
  AlertIcon,
  Checkbox,
  AlertDialog,
  AlertDialogOverlay,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogBody,
  AlertDialogFooter,
} from "@chakra-ui/react";
import { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { AddIcon, DeleteIcon, MinusIcon } from "@chakra-ui/icons";
import { useForm } from "react-hook-form";
import { Header } from "../../components/Header";
import { HelmetTitle } from "../../components/HelmetTitle";
const currentPriority = "Priority";
export const Todos = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    getValues,
  } = useForm({
    defaultpri: { Priority: "" },
  });
  const onSubmit = (data) => {
    const todo = data;
    console.log(todos);
    setTodos([
      ...todos,
      //       .
      //     
      {
        id: Date.now(),
        Schedule: todo.Schedule,
        Priority: todo.Priority,
        finish: false,
      },
    ]);
    console.log(todos);
  };
  const onChanges = (id) => {
    setTodos(
      todos.map((data) =>
        data.id === id ? { ...data, finish: !data.finish } : data
      )
    );
  };
  const onClickDelete = (id) => {
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  const today = new Date();
  const cancelRef = useRef();
  const { id } = useParams();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [value, setValue] = useState("");
  const [currentID, setCurrentID] = useState();
  const [userdata, SetData] = useState(() => {
    const data = localStorage.getItem(id);
    return data ? JSON.parse(data) : [];
  });
  const [todos, setTodos] = useState(() => {
    const getTodo = localStorage.getItem(userdata.index);
    return getTodo ? JSON.parse(getTodo) : [];
  });
  useEffect(() => {
    localStorage.setItem(userdata.index, JSON.stringify(todos));
  }, [todos]);
  return (
    <>
      <Container height={"100vh"}>
        <HelmetTitle title={"Todo"} />
        <Header Username={userdata.username} />
        <Text></Text>
        <Text>
          {today.getMonth()}{today.getDay()}
        </Text>

        <Accordion allowToggle>
          <AccordionItem>
            {({ isExpanded }) => (
              <>
                <AccordionButton>
                  <Box as="span" flex={1} textAlign={"left"}>
                     
                  </Box>
                  {isExpanded ? (
                    <MinusIcon fontSize="12px" />
                  ) : (
                    <AddIcon fontSize="12px" />
                  )}
                </AccordionButton>
                <AccordionPanel pb={4}>
                  <Box as="form" onSubmit={handleSubmit(onSubmit)}>
                    <Input
                      placeholder=""
                      {...register("Schedule", {
                        required: " ",
                      })}
                    />
                    {errors?.Schedule?.message && (
                      <Alert status="error" borderRadius={10}>
                        <AlertIcon />
                        {errors?.Schedule?.message}
                      </Alert>
                    )}
                    <RadioGroup onChange={setValue} value={value}>
                      <Stack direction={"row"}>
                        <Radio
                          colorScheme="green"
                          value=""
                          {...register(currentPriority)}
                        >
                          
                        </Radio>
                        <Radio
                          colorScheme="yellow"
                          value=""
                          {...register(currentPriority)}
                        >
                          
                        </Radio>
                        <Radio
                          colorScheme="red"
                          value=""
                          {...register(currentPriority)}
                        >
                          
                        </Radio>
                      </Stack>
                    </RadioGroup>
                    <Button type="submit"></Button>
                  </Box>
                </AccordionPanel>
              </>
            )}
          </AccordionItem>
        </Accordion>
        <VStack>
          {userdata &&
            todos.map((rsl) => (
              <Box key={rsl.id}>
                <Checkbox
                  p={"15px"}
                  size={"lg"}
                  isChecked={rsl.finish}
                  onChange={() => onChanges(rsl.id)}
                />
                {rsl.Schedule}
                <DeleteIcon
                  onClick={() => {
                    onOpen();
                    //    onOpen  isOpen   .
                    setCurrentID(rsl.id);
                    //     .
                  }}
                />
                <RadioGroup value={rsl.Priority}>
                  <Stack direction={"row"}>
                    <Radio colorScheme="green" value="">
                      
                    </Radio>
                    <Radio colorScheme="yellow" value="">
                      
                    </Radio>
                    <Radio colorScheme="red" value="">
                      
                    </Radio>
                  </Stack>
                </RadioGroup>
              </Box>
            ))}
        </VStack>
        <AlertDialog isOpen={isOpen}>
          {/*   isOpen */}
          <AlertDialogOverlay>
            <AlertDialogContent>
              <AlertDialogHeader> </AlertDialogHeader>

              <AlertDialogBody> ?</AlertDialogBody>
              <AlertDialogFooter>
                <Button ref={cancelRef} onClick={onClose}>
                  
                </Button>
                <Button
                  onClick={() => {
                    onClickDelete(currentID);
                    onClose();
                    //onClose .
                  }}
                >
                  
                </Button>
              </AlertDialogFooter>
            </AlertDialogContent>
          </AlertDialogOverlay>
        </AlertDialog>
      </Container>
    </>
  );
};
